.search {
  display: flex;
  justify-content: space-between;
  padding: 18px 8px;
  &__block {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    input {
      padding: 10px 35px 10px 40px;
      width: 100%;
      height: 40px;
      background: #f6f4f2;
      border-radius: 3px;
      outline: none;
      border: none;
      font-size: 16px;
      &::placeholder {
        font-size: 16px;
        color: #a59c94;
      }
    }
  }
  &__icon {
    position: absolute;
    margin-left: 10px;
  }
  &__close {
    display: none;
    cursor: pointer;
    position: absolute;
    right: 10px;
  }
}
